import React from 'react';
import toastService from '../../../../services/toast/toastService';
import passbookRepository from '../../../../repositories/passbookRepository';
import { Box, Paper, Grid, TextField, FormControlLabel, Checkbox } from '@mui/material';
import { LoadingButton } from '@mui/lab';
import { FaCheckCircle } from 'react-icons/fa';
import { BiCircle } from 'react-icons/bi';

export default function PassbookDetailsPage(props) {

    const memberAccount = props.selectedAccount;
    const [passbookReplace, setPassbookReplace] = React.useState(false);
    const [remarks, setRemarks] = React.useState(memberAccount?.data?.passbook?.remarks ?? "");
    const [reasons, setReasons] = React.useState("");
    const [loading, setLoading] = React.useState(false);

    const handleReplace = async () => {
        try {
            setLoading(true);
            let data = {
                account_number: memberAccount?.account_number,
                remarks: reasons,
            }

            let response = await passbookRepository.replace(data);

            if (!response.error) {

                toastService.success(response.message);
                if (props.loadAccounts) {
                    props.loadAccounts();
                }

                if (props.toggle) {
                    props.toggle();
                }
            } else {
                toastService.error(response.message);
            }

        } catch (e) {
            toastService.error(e.toString());
        } finally {
            setLoading(false);
        }
    }

    return (
        <div style={{ marginTop: -5 }}>
            <Box display="flex" flexDirection="column" sx={{ mb: 1 }}>
                <FormControlLabel
                    control={
                        <Checkbox
                            icon={<BiCircle size={20} />}
                            checkedIcon={<FaCheckCircle size={20} />}
                            checked={passbookReplace}
                            onChange={(e, c) => setPassbookReplace(c)}
                        />
                    } label={"Replace Passbook"}
                />
                <Paper square sx={{ mt: 1, mb: 1, p: 2 }}>
                    <Grid container spacing={1}>
                        <Grid item xs={12}>
                            <TextField
                                variant='standard'
                                label="Member"
                                value={memberAccount?.applicant}
                                inputProps={{
                                    readOnly: true
                                }}
                                InputLabelProps={{
                                    style: { fontSize: 18, fontWeight: "bolder", color: "#00BFA5" },
                                }}
                                fullWidth
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                variant='standard'
                                label="Account Number"
                                value={memberAccount?.account_number}
                                inputProps={{
                                    readOnly: true
                                }}
                                InputLabelProps={{
                                    style: { fontSize: 18, fontWeight: "bolder", color: "#00BFA5" },
                                }}
                                fullWidth
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                variant='standard'
                                label="Passbook Number"
                                value={memberAccount?.data?.passbook?.passbook_number}
                                inputProps={{
                                    readOnly: true
                                }}
                                InputLabelProps={{
                                    style: { fontSize: 18, fontWeight: "bolder", color: "#00BFA5" },
                                }}
                                fullWidth
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                variant='standard'
                                label="Number of times replaced"
                                value={memberAccount?.data?.passbook?.replacement_count}
                                inputProps={{
                                    readOnly: true
                                }}
                                InputLabelProps={{
                                    style: { fontSize: 18, fontWeight: "bolder", color: "#00BFA5" },
                                }}
                                fullWidth
                            />
                        </Grid>
                        <Grid item xs={12}>
                            {!passbookReplace &&
                                <TextField
                                    variant='standard'
                                    label="Remarks"
                                    placeholder='Write your remarks or notes here...'
                                    value={remarks}
                                    onChange={(e) => setRemarks(e.target.value)}
                                    fullWidth
                                    sx={{ mt: 1 }}
                                    minRows={2}
                                    maxRows={2}
                                    InputLabelProps={{
                                        style: { fontSize: 18, fontWeight: "bolder", color: "#00BFA5" },
                                    }}
                                />
                            }
                            {passbookReplace &&
                                <TextField
                                    multiline
                                    variant='outlined'
                                    label="Reasons"
                                    placeholder='Write the reasons or notes for replacement...'
                                    value={reasons}
                                    onChange={(e) => setReasons(e.target.value)}
                                    fullWidth
                                    sx={{ mt: 2 }}
                                    minRows={2}
                                    maxRows={2}
                                />
                            }
                        </Grid>
                    </Grid>
                </Paper>
            </Box>
            {passbookReplace &&
                <Box display="flex" flexDirection="row" justifyContent="flex-end">
                    <LoadingButton loading={loading} variant='contained' sx={{ minWidth: 150 }} onClick={handleReplace}>Replace</LoadingButton>
                    <LoadingButton loading={loading} onClick={props.toggle} color="secondary" variant='contained' sx={{ ml: 1, minWidth: 150 }}>Cancel</LoadingButton>
                </Box>
            }
        </div>
    )
}
