import React from 'react';
import { format, parseISO } from 'date-fns';
// Utilities 
import utilityService from '../../../../services/utils/utilityServices';
import toastService from '../../../../services/toast/toastService';
import alertService from '../../../../services/alert/alertService';
import accountsRepository from '../../../../repositories/accountsRepository';
// Redux
import { useSelector, useDispatch } from 'react-redux';
import { get, search } from '../../../../redux/async/accountsAsync';
import { setSelectedAccountType } from '../../../../redux/slices/accountsSlice';
// MUI Components
import { DataGrid, GridActionsCellItem } from '@mui/x-data-grid';
import { Box, TextField, Typography, Divider, Tooltip, Button } from '@mui/material';
import { LoadingButton } from '@mui/lab';
// Page Components
import GridLoadingOverlay from '../../grid/GridLoadingOverlay';
import GridNoRowOverlay from '../../grid/GridNoRowOverlay';
import Toolbar from '../../grid/Toolbar';
import SearchAccounts from '../../forms/fields/SearchAccounts';
import StatusCheckboxCircle from '../../forms/fields/StatusCheckboxCircle';
import MemberAccountModal from '../../modals/MemberAccountModal';
import PassbookModal from '../../modals/PassbookModal';
// react-icons
import { FaSlidersH, FaSearch, FaUniversity, FaBookOpen } from 'react-icons/fa';
import DateRange from '../../forms/fields/DateRange';

const LoadingOverlayOverride = () => {
    return (
        <GridLoadingOverlay />
    );
}

const NoRowOverlayOverride = () => {
    return (
        <GridNoRowOverlay />
    );
}

const ToolbarOverride = (props) => {
    return (
        <Toolbar />
    );
}

const ActionsIcon = () => {
    return <FaSlidersH />
}


export default function AccountsDataTable(props) {

    const dispatch = useDispatch();
    const accountsRedux = useSelector((state) => state.accounts.value);
    const masterfiles = useSelector((state) => state.masterfiles.value).masterfiles;
    const dateRedux = useSelector((state) => state.dates.value);

    const accountTypes = masterfiles?.accountTypes;
    const type = accountsRedux?.type;
    const loading = accountsRedux.loading;

    const startDate = dateRedux?.startDate;
    const endDate = dateRedux?.endDate;

    const [memberAccount, setMemberAccount] = React.useState(null);
    const [searchBy, setSearchBy] = React.useState("Member");
    const [keyword, setKeyword] = React.useState("");
    const [isSearch, setIsSearch] = React.useState(false);
    const [passbookModal, setPassbookModal] = React.useState(false);
    const togglePassbookModal = () => setPassbookModal(!passbookModal);
    const [accountDetailsModal, setAccountDetailsModal] = React.useState(false);
    const toggleAccountDetailsModal = () => setAccountDetailsModal(!accountDetailsModal);

    const width = 170;

    const actions = [
        {
            label: "Account Details",
            icon: <FaUniversity size={18} />,
            event: (data) => handleViewDetails(data),
        },
        {
            label: "Passbook",
            icon: <FaBookOpen size={18} />,
            event: (data) => handlePassbook(data),
        },
    ]

    const columns = [
        {
            field: 'applicant', headerName: 'Member', width: 300, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.applicant === null ? "..." : params.row.applicant}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'account_number', headerName: 'Account Number', width: 250, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.account_number === null ? "..." : params.row.account_number}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'balance', headerName: 'Account Balance', width: 200, renderCell: (params) => {

                let balance = utilityService.numberWithCommas(parseFloat(params.row.balance).toFixed(2));

                return (
                    <div>
                        <span>
                            PHP {balance === "" ? "..." : balance}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'is_active', headerName: 'Status', headerAlign: 'center', align: 'center', width: 140, renderCell: (params) => {
                let status = 'In-active';
                let color = "#E45570";

                if (params.row.is_active === 1) {
                    status = "Active";
                    color = "#00BFA5";
                }

                return (
                    <div style={{
                        borderWidth: 2,
                        borderStyle: 'solid',
                        borderRadius: 7,
                        borderColor: color,
                        backgroundColor: color,
                        color: "#FFFFFF",
                        height: 25,
                        width: '70%',
                        textAlign: 'center',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                    }}>
                        <Tooltip arrow placement='top' title={params.row.is_active === 1 ? "Click to de-activate account" : "Click to activate account ?"}>
                            <Button
                                onClick={() => handleAccountStatus(params.row.account_number, params.row.is_active)}
                                variant='text'
                                sx={{ color: "#FFF", fontSize: 12 }}
                            >
                                {status}
                            </Button>
                        </Tooltip>
                    </div>
                );
            }
        },

        {
            field: 'with_passbook', headerName: 'With Passbook', headerAlign: 'center', align: 'center', width: 200, renderCell: (params) => {

                let status = 'No';
                let color = "#FFA117";

                if (params.row.with_passbook === true) {
                    status = "Yes";
                    color = "#00BFA5";
                }

                return (
                    <div style={{
                        borderWidth: 2,
                        borderStyle: 'solid',
                        borderRadius: 7,
                        borderColor: color,
                        backgroundColor: color,
                        color: "#FFFFFF",
                        height: 25,
                        width: '70%',
                        textAlign: 'center',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                    }}>
                        {status}
                    </div>
                );
            }
        },
        {
            field: 'date_created', headerName: 'Date Created', width: width, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.date_created === null ? "..." : format(parseISO(params.row.date_created), "MMMM dd, yyyy")}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'actions', headerName: 'Options', flex: true, type: 'actions', getActions: (params) => {
                return actions.map((data, index) => {
                    return <GridActionsCellItem
                        key={`gridCell-${index}`}
                        icon={data.icon}
                        onClick={() => data.event(params.row)}
                        label={data.label}
                        sx={{
                            '&:hover': {
                                color: data.color ?? "#00BFA5"
                            }
                        }}
                        showInMenu
                    />
                });
            }
        },
    ];

    const handleViewDetails = (data) => {
        setMemberAccount(data);
        toggleAccountDetailsModal();
    }

    const handlePassbook = (data) => {
        setMemberAccount(data);
        togglePassbookModal();
    }

    const handleAccountStatus = async (account_number, status) => {

        try {
            let title = "Activate account";
            let newStatus = true;
            if (status === 1) {
                title = "De-activate account";
                newStatus = false;
            }

            let confirm = await alertService.confirm({ title: title, text: "Are you sure?" });

            if (confirm.isConfirmed) {

                let data = {
                    "account_number": account_number,
                    "status": newStatus
                }

                let response = await accountsRepository.changeStatus(data);

                if (!response.error) {
                    loadAccounts();
                    toastService.success(response.message);
                } else {
                    toastService.error(response.message);
                }
            }
        } catch (e) {
            toastService.error(e.toString());
        }

    }

    const handleCode = (code) => {
        console.log(code);
        dispatch(setSelectedAccountType(code));
    }

    const handleKeyword = (keyword) => {
        setKeyword(keyword);
        if (keyword === "") {
            loadAccounts();
        }
    }

    const handleSearch = async () => {

        if (keyword === "") {
            return false;
        }

        try {
            setIsSearch(true);
            let data = {
                account_type: type,
                startDate: new Date(startDate),
                endDate: new Date(endDate),
            }

            if (searchBy.toLowerCase() === "name") {
                data.name = keyword;
            } else {
                data.account_number = keyword;
            }

            await dispatch(search(data)).unwrap();

        } catch (e) {
            toastService.alert(e.toString());
        } finally {
            setIsSearch(false);
        }
    };

    const loadAccounts = React.useCallback(() => {

        let data = {
            account_type: type,
            startDate: new Date(startDate),
            endDate: new Date(endDate),
        }

        dispatch(get(data));

    }, [dispatch, startDate, endDate, type])

    React.useEffect(() => {
        let mounted = false;

        if (!mounted) {
            loadAccounts();
        }

        return () => {
            mounted = true;
        }
    }, [loadAccounts]);

    return (
        <div style={{ maxHeight: 600, minHeight: 600, width: '100%' }}>
            {passbookModal && <PassbookModal open={passbookModal} toggle={togglePassbookModal} selectedAccount={memberAccount} loadAccounts={loadAccounts} />}
            {accountDetailsModal && <MemberAccountModal open={accountDetailsModal} toggle={toggleAccountDetailsModal} selectedAccount={memberAccount} />}
            <Box display="flex" flextDirection="row" justifyContent="space-between">
                <DateRange label="Date Created" />
                <Box display="flex" flextDirection="row" >
                    <TextField
                        type="search"
                        variant="outlined"
                        size="small"
                        onChange={(e) => handleKeyword(e.target.value)}
                        placeholder={`Search ${utilityService.capitalizeFirstLetter(searchBy)}`}
                        InputProps={{
                            endAdornment: <SearchAccounts label={searchBy} setLabel={setSearchBy} />
                        }}
                        sx={{
                            mb: 1,
                            minWidth: 400
                        }}
                    />
                    <LoadingButton type="button" loading={isSearch} onClick={handleSearch} variant="contained" size="small" sx={{ height: 38, ml: 1, minWidth: 100 }}>
                        Search <FaSearch style={{ marginLeft: 7 }} />
                    </LoadingButton>
                </Box>
            </Box>
            <Divider />
            <Box display="flex" flextDirection="row" justifyContent="flex-start">
                <Typography variant="body1" sx={{ m: 1 }}>
                    Account Types:
                </Typography>
                {
                    accountTypes.map((data, index) => {

                        let types = data?.type?.toUpperCase();
                        let code = data?.code.toUpperCase();

                        return (
                            <div key={`statusBox-${index}`}>
                                <StatusCheckboxCircle label={types} check={data?.code.toUpperCase() === type} setValue={() => handleCode(code)} />
                            </div>
                        )
                    })
                }
            </Box>
            <div style={{ height: 450 }}>
                <DataGrid
                    rows={accountsRedux.accounts}
                    columns={columns}
                    onRowDoubleClick={(params) => handleViewDetails(params.row)}
                    loading={loading}
                    density="standard"
                    disableSelectionOnClick
                    disableColumnMenu
                    components={{
                        LoadingOverlay: LoadingOverlayOverride,
                        NoRowsOverlay: NoRowOverlayOverride,
                        MoreActionsIcon: ActionsIcon,
                        Toolbar: ToolbarOverride
                    }}
                />
            </div>

        </div>
    );
}