import React from 'react';
// MUI Components
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
// react-icons 
import { FaCheckCircle } from 'react-icons/fa';
import { BiCircle } from 'react-icons/bi';

export default function StatusCheckboxCircle(props) {
 
    const handleChange = () => {   
        if(props.setValue){
            props.setValue(props.value);
        }
    }

    return (
        <div>
            <FormControlLabel
                control={
                    <Checkbox
                        icon={<BiCircle size={20} />}
                        checkedIcon={<FaCheckCircle size={20} />}
                        checked={props.check}
                        onChange={() => handleChange()}
                    />
                } label={props.label ?? "Set label"} />
        </div>
    )
}
