import React from 'react';
// Services
import utilityService from '../../../services/utils/utilityServices';
// Modal Components
import MenuDialog from '../shared/Dialog/MenuDialog';
// MUI Components 
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import { TextField } from '@mui/material';
// react-icons 

export default function MemberAccountModal(props) {

    const options = [
        {
            label: "Account Number",
            value: props.selectedAccount?.account_number ?? "...",
        },
        {
            label: "Type",
            value: utilityService.capitalizeFirstLetter(props.selectedAccount?.account_type) ?? "...",
        },
        {
            label: "Date Opened",
            value: props.selectedAccount?.openingDate ?? "...",
        },
        {
            label: "Balance",
            value: `PHP ${utilityService.numberWithCommas(props.selectedAccount?.balance)}` ?? "...",
        },
        {
            label: "Legacy Account",
            info: "An account created from old system",
            value: props.selectedAccount?.data?.is_legacy ? "Yes" : "No",
        },
    ]

    return (
        <div>
            <MenuDialog
                open={props.open}
                toggle={props.toggle}
                title="Account Details"
            >
                <Box display="flex" alignItems="center" flexDirection="column" sx={{ p: 0, m: 0 }}>
                    <Paper sx={{ minWidth: 500, p: 2, mt: -2 }} square elevation={1}>
                        {
                            options.map((data, index) => {
                                return (
                                    <TextField
                                        key={`accountDetails-${index}`}
                                        variant='standard'
                                        label={data.label}
                                        value={data.value}
                                        inputProps={{
                                            readOnly: true
                                        }}
                                        InputLabelProps={{
                                            style: { fontSize: 18, fontWeight: "bolder", color: "#00BFA5" },
                                        }}
                                        fullWidth
                                        sx={{ mt: 1.5 }}
                                    />
                                );
                            })
                        }
                        <TextField
                            fullWidth
                            variant='standard'
                            value={props?.selectedAccount?.remarks}
                            label="Remarks"
                            sx={{ mt: 2 }}
                            maxRows={5}
                            minRows={5}
                            inputProps={{
                                readOnly: true
                            }}
                            InputLabelProps={{
                                style: { fontSize: 18, fontWeight: "bolder", color: "#00BFA5" },
                            }}
                        />
                    </Paper>
                </Box>
            </MenuDialog>
        </div >
    )
}
