import React from 'react'
import PassbookForm from '../forms/PassbookForm';
import PassbookDetailsPage from '../pages/common/PassbookDetailsPage';
import LargeModalDialog from '../shared/Dialog/LargeModalDialog';


export default function PassbookModal(props) {
    const passbookStatus = props.selectedAccount?.data?.passbook !== null && props.selectedAccount?.data?.passbook !== undefined;
    return (
        <div>
            <LargeModalDialog open={props.open} toggle={props.toggle} title={passbookStatus ? "Passbook Details" : "New Passbook"} size="sm">
                {!passbookStatus &&
                    <PassbookForm toggle={props.toggle} selectedAccount={props.selectedAccount} loadAccounts={props.loadAccounts} />
                }
                {passbookStatus &&
                    <PassbookDetailsPage toggle={props.toggle} selectedAccount={props.selectedAccount} loadAccounts={props.loadAccounts} />
                }
            </LargeModalDialog>
        </div >
    )
}
