import React from 'react';
import { format } from 'date-fns';
import { useDispatch, useSelector } from 'react-redux';
import { setEndDate, setStartDate } from '../../../../redux/slices/dateSlice';
import { Box, TextField, IconButton, Typography, Tooltip, Button } from '@mui/material';
import DateRangeModal from '../../modals/DateRangeModal';
import { FaRedo, FaTimes } from 'react-icons/fa';
import { MdEditCalendar } from 'react-icons/md';

export default function DateRange(props) {

    const dispatch = useDispatch();
    const dateRedux = useSelector((state) => state.dates.value);
    const [dateRange, setDateRange] = React.useState("");
    const [dateRangeModal, setDateRangeModal] = React.useState(false);
    const toggleDateRangeModal = () => setDateRangeModal(!dateRangeModal);

    const startDate = dateRedux?.startDate;
    const endDate = dateRedux?.endDate;

    const handleApplyDates = (data) => {

        let selectedDateRange = `${format(data.startDate, 'M/dd/yyyy')} to ${format(data.endDate, 'M/dd/yyyy')}`;
        setDateRange(selectedDateRange);

        dispatch(setStartDate(format(data.startDate, 'M/dd/yyyy')));
        dispatch(setEndDate(format(data.endDate, 'M/dd/yyyy')));

        if (props.handleApply) {
            props.handleApply(data);
        }

        toggleDateRangeModal();
    }

    const handleResetDate = (e) => {
        e.stopPropagation();
        setDateRange("");
        dispatch(setStartDate(""));
        dispatch(setEndDate(""));
    }

    React.useEffect(() => {
        let mounted = false;

        if (!mounted) {
            let dateRange = '';

            if (props.reset) {
                handleResetDate();
            } else {
                if (startDate !== '') {
                    dateRange = `${startDate} to ${endDate}`;
                }

                setDateRange(dateRange);
            }

        }

        return () => {
            mounted = true;
        }

    }, [startDate, endDate]);

    return (
        <Box display="flex" flextDirection="row" >
            {dateRangeModal && <DateRangeModal open={dateRangeModal} toggle={toggleDateRangeModal} handleApply={handleApplyDates} />}
            <TextField
                value={dateRange}
                sx={{ minWidth: 400 }}
                size='small'
                placeholder='Select Date'
                InputProps={{
                    startAdornment: (
                        <IconButton
                            disableFocusRipple
                            disableRipple
                            sx={{
                                borderRadius: 0,
                                borderTopLeftRadius: 5,
                                borderBottomLeftRadius: 5,
                                mt: 0,
                                ml: -2,
                                mr: 1,
                                backgroundColor: "#EEE",
                                color: "#000",
                                minWidth: 150
                            }}
                        >
                            <Typography>
                                {props.label ?? "Date Applied"}:
                            </Typography>
                        </IconButton>
                    ),
                    endAdornment: (
                        <IconButton onClick={(e) => dateRange !== "" ? handleResetDate(e) : null}>
                            {dateRange === "" ? <MdEditCalendar size={18} /> : <FaTimes size={15} />}
                        </IconButton>
                    )
                }}
                inputProps={{
                    readOnly: true
                }}
                onClick={toggleDateRangeModal}
            />
        </Box>
    )
}
