import React from 'react';
import { Box, Grid, Paper, TextField } from '@mui/material'; 
import { LoadingButton } from '@mui/lab';
import toastService from '../../../services/toast/toastService';
import passbookRepository from '../../../repositories/passbookRepository';

export default function PassbookForm(props) {

    const memberAccount = props.selectedAccount;

    const [remarks, setRemarks] = React.useState("");
    const [loading, setLoading] = React.useState(false);

    const handleNew = async () => {
        try {
            setLoading(true);
            let data = {
                uuid: memberAccount?.data?.uuid,
                account_number: memberAccount?.account_number,
                account_type_code: memberAccount?.account_type,
                remarks: remarks,
            }

            let response = await passbookRepository.new(data);

            if(!response.error){
                toastService.success(response.message);
                if(props.loadAccounts){
                    props.loadAccounts();
                }

                if(props.toggle){
                    props.toggle();
                }    
            }else{
                toastService.error(response.message);
            }

        } catch (e) {
            toastService.error(e.toString());
        } finally {
            setLoading(false);
        }
    }

    return (
        <div style={{ marginTop: -12 }}>
            <Box display="flex" flexDirection="column" sx={{ mb: 1 }}>
                <Paper square sx={{ mt: 1, mb: 1, p: 2 }}>
                    <Grid container spacing={1}>
                        <Grid item xs={12}>
                            <TextField
                                variant='standard'
                                label="Member"
                                value={memberAccount?.applicant}
                                inputProps={{
                                    readOnly: true
                                }}
                                fullWidth
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                variant='standard'
                                label="Account Number"
                                value={memberAccount?.account_number}
                                inputProps={{
                                    readOnly: true
                                }}
                                fullWidth
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                multiline
                                variant='outlined'
                                label="Remarks"
                                placeholder='Write your remarks or notes here...'
                                value={remarks}
                                onChange={(e) => setRemarks(e.target.value)}
                                fullWidth
                                sx={{ mt: 1 }}
                                minRows={2}
                                maxRows={2}
                            />
                        </Grid>
                    </Grid>
                </Paper>
            </Box>
            <Box display="flex" flexDirection="row" justifyContent="flex-end">
                <LoadingButton loading={loading} variant='contained' sx={{ minWidth: 150 }} onClick={handleNew}>Issue</LoadingButton>
                <LoadingButton loading={loading} onClick={props.toggle} color="secondary" variant='contained' sx={{ ml: 1, minWidth: 150 }}>Cancel</LoadingButton>
            </Box>
        </div>
    )
}

